import { useState } from "react";
import styles from "./loglist.module.css";
import SyntaxHighlighter from 'react-syntax-highlighter';
import { atomOneDark } from 'react-syntax-highlighter/dist/esm/styles/hljs';
import { TransferListData } from "@mantine/core/lib/TransferList/types";

const LogList = (props: {
  data: Array<{
    timestamp: number;
    data: {
      id: string;
      legacyId: string;
      data: Object;
    };
  }>;
  filters: TransferListData;
}) => {
  return (
    <div className={styles.container}>
      {props.data.map((item, index) => {
        if (props.filters[1].length > 0) {
          if (props.filters[1].map(item => item.value).includes(item.data.legacyId + "S2CPacket")) {
            return <LogLine key={index} timestamp={item.timestamp} data={item.data} />
          }

          return null;
        }
        
        return <LogLine key={index} timestamp={item.timestamp} data={item.data} />
      })}
    </div>
  )
}

const LogLine = (props: {
  timestamp: number;
  data: {
    id: string;
    legacyId: string;
    data: Object;
  };
}) => {
  const [expanded, setExpanded] = useState(false);

  return (
    <div className={styles.line} onClick={() => setExpanded(!expanded)}>
      <div className={styles.timestamp}>{new Date(props.timestamp).toLocaleTimeString()}</div>
      <div className={styles.packet_name}>{props.data.id} <span className={styles.legacy_packet_name}>({props.data.legacyId + "S2CPacket"})</span></div>
    
      {expanded && (
        <div className={styles.expanded}>
          <SyntaxHighlighter language="json" style={atomOneDark}>
            {JSON.stringify(props.data.data, null, 2)}
          </SyntaxHighlighter>
        </div>
      )}
    </div>
  )
}

export default LogList;